@import "../../../scss/variables.scss";
@import "../../../scss/mixins.scss";

.interface__wrapper {
  width: 100%;

  .interface {
    //section2
    @include min-width($smPhoneWidth) {
      width: 100%;
    }
    @include min-width($tableWidth) {
      width: auto;
    }

    .header_mobile {
      @include min-width($tableWidth) {
        display: none;
      }
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 12px;

      & > button {
        background: none;
        border: none;
        
        .icon_rectangles {
          width: 24px;
          height: 24px;
          fill: $success-color-dark;
        }
      }
      .header__mobile_logo {
        display: flex;
        align-items: center;

        .sidebar__header_name {
          font-size: 18px;
          line-height: 22px;
          font-family: $mainFontBold;
          color: $border-lighter;
        }
        & > img {
          width: 31px;
          height: 36px;
          padding-left: 16px;
        }
      }
    }

    .header {
      @include min-width($smPhoneWidth) {
        display: flex;
        justify-content: space-between;
        padding-bottom: 8px;
      }
      @include min-width($tableWidth) {
        padding-bottom: 0px;
        max-height: 60.16px;
        height: 61px;
        max-width: get-vw14(834px);
      }
      @include min-width($desktopWidth) {
        max-width: get-vw(834px);
      }

      & > h1 {
        @include min-width($smPhoneWidth) {
          font-size: 24px;
          line-height: 29px;
          font-weight: 500;
          text-transform: none;
          color: $dark-color;
          min-width: none;
        }
        @include min-width($tableWidth) {
          font-size: get-vw14(36px);
          line-height: get-vw14(44px);
          min-width: get-vw14(300px);
        }
        @include min-width($desktopWidth) {
          font-size: get-vw(36px);
          line-height: get-vw(44px);
          min-width: get-vw(300px);
        }
      }

      .edit_btns {
        @include min-width($smPhoneWidth) {
          display: block;
        }
      }
      .btn {
        padding: 0;
        margin: 0;
        border: none;
        display: inline-flex;
        background-color: inherit;
        cursor: pointer;

        &:first-child {
          margin-right: get-vw(45px);
        }

        .icon_pencil {
          width: 18px;
          fill: $contrast-color;

          @include min-width($tableWidth) {
            width: get-vw14(24px);
          }
        }
        .icon_bucket {
          width: 15px;
          fill: $success-color-dark;

          @include min-width($tableWidth) {
            width: get-vw14(18px);
          }
        }
      }
      .icon_dots {
        @include min-width($smPhoneWidth) {
          width: 32px;
          height: 32px;
          fill: $contrast-color;
        }
        @include min-width($tableWidth) {
          display: none;
        }
      }
    }
    .content {
      @include min-width($smPhoneWidth) {
        height: 451px;
        width: 100%;
        color: $border-base;
        background-color: $border-light;
        text-align: center;
        font-weight: bold;
        font-size: get-vw(48px);
        position: relative;
      }
      @include min-width($tableWidth) {
        height: get-vw14(712px);
        width: get-vw14(834px);
      }
      @include min-width($desktopWidth) {
        height: get-vw(712px);
        width: get-vw(834px);
      }
    }
  }
  .icon_pencil,
  .icon_rectangles,
  .icon_bucket,
  .icon_dots,
  .icon_copy {
    opacity: 0.8;
    transition: 0.3s ease;
    cursor: pointer;

    &:hover {
      opacity: 1;
      transform: scale(1.1);
    }
  }
}

.groupInfo {
  @include min-width($smPhoneWidth) {
    font-size: 36px;
    line-height: 44px;
    max-width: 300px;
    position: absolute;
    top: 45%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
  @include min-width($tableWidth) {
    font-size: get-vw14(48px);
    line-height: get-vw14(59px);
  }
  @include min-width($desktopWidth) {
    font-size: get-vw(48px);
    line-height: get-vw(59px);
    max-width: none;
  }
}
